"use client"

import { useEffect, useState } from "react"
import { useRouter, useParams } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { PrivateRoute } from "@/components/auth/private-route"
import { useAuth } from "@/contexts/auth-context"
import { patientsApi, type Patient } from "@/lib/api/patients-api"
import { getErrorMessage } from "@/lib/api/axios-client"
import { useToast } from "@/hooks/use-toast"
import {
  Loader2,
  Edit,
  UserX,
  UserCheck,
  Mail,
  Phone,
  MapPin,
  Calendar,
  Users,
  UserPlus,
  UserMinus,
} from "lucide-react"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"

function PatientDetailsContent() {
  const router = useRouter()
  const params = useParams()
  const { isAdmin, user } = useAuth()
  const { toast } = useToast()
  const [patient, setPatient] = useState<Patient | null>(null)
  const [loading, setLoading] = useState(true)
  const [statusLoading, setStatusLoading] = useState(false)
  const [assignLoading, setAssignLoading] = useState(false)
  const [unassignLoading, setUnassignLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const patientId = Number(params.id)

  useEffect(() => {
    loadPatient()
  }, [patientId])

  const loadPatient = async () => {
    try {
      setLoading(true)
      const data = await patientsApi.getById(patientId)
      setPatient(data)
    } catch (err) {
      setError(getErrorMessage(err))
    } finally {
      setLoading(false)
    }
  }

  const handleToggleStatus = async () => {
    if (!patient) return

    try {
      setStatusLoading(true)
      await patientsApi.updateStatus(patient.id, !patient.active)
      await loadPatient()
    } catch (err) {
      setError(getErrorMessage(err))
    } finally {
      setStatusLoading(false)
    }
  }

  const handleAssignToMe = async () => {
    if (!patient) return

    try {
      setAssignLoading(true)
      setError(null)
      await patientsApi.assignToMe(patient.id)
      await loadPatient()
      toast({
        title: "Success",
        description: "Patient added to your list",
      })
    } catch (err) {
      setError(getErrorMessage(err))
    } finally {
      setAssignLoading(false)
    }
  }

  const handleUnassignFromMe = async () => {
    if (!patient) return

    try {
      setUnassignLoading(true)
      setError(null)
      await patientsApi.unassignFromMe(patient.id)
      // Update local state to remove current user from therapists list
      setPatient((prev) => {
        if (!prev) return prev
        return {
          ...prev,
          therapists: prev.therapists.filter((t) => t.id !== user?.id),
        }
      })
      toast({
        title: "Success",
        description: "Patient removed from your list",
      })
    } catch (err) {
      setError(getErrorMessage(err))
      toast({
        title: "Error",
        description: getErrorMessage(err),
        variant: "destructive",
      })
    } finally {
      setUnassignLoading(false)
    }
  }

  const isAlreadyAssigned = patient?.therapists?.some((therapist) => therapist.id === user?.id)

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
      </div>
    )
  }

  if (error || !patient) {
    return (
      <div className="mx-auto max-w-4xl px-4 py-8 sm:px-6 lg:px-8">
        <Alert variant="destructive">
          <AlertDescription>{error || "Patient not found"}</AlertDescription>
        </Alert>
        <Button onClick={() => router.back()} className="mt-4" variant="outline">
          Go Back
        </Button>
      </div>
    )
  }

  return (
    <div className="mx-auto max-w-4xl px-4 py-8 sm:px-6 lg:px-8">
      <div className="mb-6 flex flex-col gap-4 sm:flex-row sm:items-start sm:justify-between">
        <div>
          <h1 className="text-3xl font-bold text-foreground">{patient.fullName}</h1>
          <p className="mt-2 text-muted-foreground">Patient Details</p>
        </div>
        <div className="flex flex-wrap gap-2">
          {!isAlreadyAssigned && (
            <Button variant="outline" onClick={handleAssignToMe} disabled={assignLoading}>
              {assignLoading ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : (
                <UserPlus className="mr-2 h-4 w-4" />
              )}
              Assign to Me
            </Button>
          )}
          <Link href={`/patients/${patient.id}/edit`}>
            <Button variant="outline">
              <Edit className="mr-2 h-4 w-4" />
              Edit
            </Button>
          </Link>
          {isAdmin && (
            <Button
              variant={patient.active ? "destructive" : "default"}
              onClick={handleToggleStatus}
              disabled={statusLoading}
            >
              {statusLoading ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : patient.active ? (
                <UserX className="mr-2 h-4 w-4" />
              ) : (
                <UserCheck className="mr-2 h-4 w-4" />
              )}
              {patient.active ? "Deactivate" : "Activate"}
            </Button>
          )}
        </div>
      </div>

      {error && (
        <Alert variant="destructive" className="mb-6">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <div className="space-y-6">
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <CardTitle>Basic Information</CardTitle>
              <Badge variant={patient.active ? "default" : "secondary"}>{patient.active ? "Active" : "Inactive"}</Badge>
            </div>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center gap-3">
              <Calendar className="h-5 w-5 text-muted-foreground" />
              <div>
                <p className="text-sm font-medium text-muted-foreground">Date of Birth</p>
                <p className="text-base">{new Date(patient.dateOfBirth).toLocaleDateString()}</p>
              </div>
            </div>

            {patient.phone && (
              <div className="flex items-center gap-3">
                <Phone className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Phone</p>
                  <p className="text-base">{patient.phone}</p>
                </div>
              </div>
            )}

            {patient.email && (
              <div className="flex items-center gap-3">
                <Mail className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Email</p>
                  <p className="text-base">{patient.email}</p>
                </div>
              </div>
            )}

            {patient.address && (
              <div className="flex items-center gap-3">
                <MapPin className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Address</p>
                  <p className="text-base">{patient.address}</p>
                </div>
              </div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Users className="h-5 w-5" />
              Assigned Therapists
            </CardTitle>
          </CardHeader>
          <CardContent>
            {patient.therapists && patient.therapists.length > 0 ? (
              <div className="flex flex-wrap gap-2">
                {patient.therapists.map((therapist) => (
                  <div key={therapist.id} className="flex items-center gap-2">
                    <Badge variant="secondary" className="px-3 py-1">
                      {therapist.fullName}
                      {therapist.id === user?.id && " (You)"}
                    </Badge>
                    {/* Show unassign button only for current user */}
                    {therapist.id === user?.id && (
                      <AlertDialog>
                        <AlertDialogTrigger asChild>
                          <Button variant="destructive" size="sm" disabled={unassignLoading}>
                            {unassignLoading ? (
                              <Loader2 className="mr-1 h-3 w-3 animate-spin" />
                            ) : (
                              <UserMinus className="mr-1 h-3 w-3" />
                            )}
                            Remove from your patients
                          </Button>
                        </AlertDialogTrigger>
                        <AlertDialogContent>
                          <AlertDialogHeader>
                            <AlertDialogTitle>Remove patient from your list?</AlertDialogTitle>
                            <AlertDialogDescription>
                              Are you sure you want to remove this patient from your list? You can always add them back
                              later.
                            </AlertDialogDescription>
                          </AlertDialogHeader>
                          <AlertDialogFooter>
                            <AlertDialogCancel>Cancel</AlertDialogCancel>
                            <AlertDialogAction
                              onClick={handleUnassignFromMe}
                              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                            >
                              Remove
                            </AlertDialogAction>
                          </AlertDialogFooter>
                        </AlertDialogContent>
                      </AlertDialog>
                    )}
                  </div>
                ))}
              </div>
            ) : (
              <p className="text-sm text-muted-foreground">No therapists assigned yet.</p>
            )}
          </CardContent>
        </Card>

        {patient.generalNotes && (
          <Card>
            <CardHeader>
              <CardTitle>General Notes</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="whitespace-pre-wrap text-muted-foreground">{patient.generalNotes}</p>
            </CardContent>
          </Card>
        )}

        <Card>
          <CardHeader>
            <CardTitle>System Information</CardTitle>
          </CardHeader>
          <CardContent className="space-y-2 text-sm text-muted-foreground">
            <p>Created: {new Date(patient.createdAt).toLocaleString()}</p>
            <p>Last Updated: {new Date(patient.updatedAt).toLocaleString()}</p>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}

export default function PatientDetailsPage() {
  return (
    <PrivateRoute>
      <PatientDetailsContent />
    </PrivateRoute>
  )
}
